import React, { useEffect, useState } from 'react'
import ImageSlider from '../../components/ImageSlider'
import CardItem from './Components/CardItem';
import styled from 'styled-components';
import { primary, primaryLight, secondaryDark, secondaryLight } from '../../color';
import { fetchData } from '../../utils/fetchDatas';
import MyChart from '../../components/Chart';

///////TODO:
///////  
///////  (    )

/* < >
 
 
  
 
, 
  
*/


//
const GameListContainer = styled.div`
  padding-top: 64px;
  padding-bottom: 64px;
  padding-left: 32px;
  padding-right: 32px; 
  background-color: ${primaryLight}
`
// 
const MenuText = styled.span`
  color: ${primary};
  font-size: 24px;
  font-style: normal;
  font-weight: bold;
  line-height: normal;
`
const Line = styled.div`
  width: 100%;
  height: ${(props) => (props.h ? props.h : '2px')};
  background: ${(props) => (props.c ? props.c : 'rgba(0,0,0,0.2)')};
`
// 
const Bottom = styled.div`
  display: flex;
  width: 100%;
  height: 100%;
  background-color: none;
`
const CategoryList = styled.div`
  width: auto;
  height 100%;
  padding: 16px 0px;
  
  display: flex;
  flex-direction: column;
  align-items: start;
  gap: 0px;
`
const CategoryItem = styled.span`
  background-color: ${props => props.picked===1 ? secondaryLight : primaryLight};
  color: ${props => props.picked===1 ? 'white' : primary};
  width: 100%;
  padding: 2px 8px;
  border-radius: 1rem 0 0 1rem;

  transition: background-color 0.3s;
  &:hover{
    background-color: ${secondaryDark};
    color: white;
    cursor: pointer;
  }
`
const CategoryItemText = styled.span `
  font-size: 14px;
  font-style: normal;
  font-weight: normal;
  white-space: nowrap;
  pointer-events: none;
`
//  
const CardList = styled.div`
  width: 100%;
  height 100%;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 32px;
  place-items: start;
  justify-self: start;
  padding-top: 32px;
  padding-left: 64px;
  position: relative;
`
const MoreButton = styled.button`
  position: absolute;
  bottom: -80px;
  left: 48%;
  font-size: 1rem;
  font-weight: bold;
  color: white;
  background: ${secondaryLight};
  border-radius: 20px; 
  border: none;
  padding: 4px 12px;
  cursor: pointer; 
`

//  
const GameChartContainer = styled.div`
  padding-top: 64px;
  padding-bottom: 64px;
  padding-left: 32px;
  padding-right: 32px; 
  width: 100%;
  height: auto;
`
const ChartContainer = styled.div`
  display: flex;
  width: 100%;
  height: auto;
  align-items: center;
  justify-content: center;
  background-color: white;
  margin: 32px 0px;
`
const ChartDiv = styled.div`
  display: block;
  margin: 40px;
`

const MainPage = () => {
  const [banners, setBanners] = useState([]);
  const [products, setProducts] = useState([]);
  const [tags, setTags] = useState([]); 
  const [category, setCategory] = useState(0);
  const [filteredProducts, setFilteredProducts] = useState([]);
  const [page, setPage] = useState(1); //
  const [cnt, setCnt] = useState([]); //  
  const itemsPerPage = 6;
  
  ////(tags)  
  function getTags(items){
    ////   
    let temp_category = [];
    items.forEach((item) => {
      item.tag.forEach((tag) => {
        if(!temp_category.includes(tag)){
          temp_category.push(tag);
        }
      })
    })
    //   
    temp_category.sort(); // 
    temp_category.sort((a, b) => {  // 
      let aa = a.charAt(a.length-1);
      let bb = b.charAt(a.length-1);
      if(((aa=="")&&(bb!=""))||((aa=="")&&(bb==""))){
        return 1
      }
      else{
        return -1
      }
    });
    setTags(["", ...temp_category]);

    
    ////  
    let temp_cnt = Array(temp_category.length+1).fill(0);
    temp_cnt[0] = items.length;
    items.forEach((item) => {
      item.tag.forEach((tag) => {
        for(let i=0; i<=temp_category.length; i++){ //==  +1 
          if(temp_category[i] == tag){
            temp_cnt[i+1]++;
          }
        }
      })
    })
    setCnt([items.length, ...temp_cnt]);
  }
 
  ////  
  function onChangeCategory(index){
    setCategory(index);
    if(index!=0){
      let temp = []
      products.forEach(product=> {
        if(product.tag.includes(tags[index])){
          temp.push(product);
        }
      })
      setFilteredProducts(temp);
    }else{
      setFilteredProducts(products);
    }
    setPage(1); //  
  }
  
  ///   
  function onMore(){
     if(page*itemsPerPage - filteredProducts.length < 0){
       setPage(page+1);
     }
  }

  useEffect(()=>{
    fetchData()
    .then((data) => {
      setProducts(data.products); //  
      setBanners(data.banners); //  
      getTags(data.products); //    
      setFilteredProducts(data.products); //  
    }).catch(error => console.log("  ", error));
  },[]);

  const about_platform = {
    labels: ["PC","",""],
    datasets: [
      {
        data: [cnt[cnt.length-3],cnt[cnt.length-2],cnt[cnt.length-1]],
        backgroundColor:['rgba(255, 99, 132, 0.2)','rgba(54, 162, 235, 0.2)','rgba(255, 206, 86, 0.2)']
      },
    ],
  };
  const about_genre = {
    labels: tags.slice(1, tags.length-3),
    datasets:[
      {
        data: cnt.slice(2,cnt.length-3),
        backgroundColor:['#FF8080','#CDFAD5','#FFF89A','#9ADCFF','#C1AEFC']
      }
    ]
  };

  if(!products || !tags){
    return(<div>...</div>)
  }else{
    console.log(tags, cnt);
    return (
      <>
          {/*    */ }
          <ImageSlider items={banners}/>
          {/* Game  */}
          <GameListContainer>
            <div>
              <MenuText>GAME LIST</MenuText>
              <Line/>
            </div>
            <Bottom>
              {/*  */}
              <CategoryList>
                {tags.map((tag, index) => (
                  <CategoryItem key={tag} 
                    picked={index===category? 1:0}
                    onClick={()=>onChangeCategory(index)}>
                    <CategoryItemText>{tag}</CategoryItemText>
                  </CategoryItem>
                ))}
              </CategoryList>
              {/*  */}
              <CardList>
                {/*  */}
                {filteredProducts.map((product, index) => (
                  (index<page*itemsPerPage) ? //  
                    <CardItem key={index} 
                      id={product.id}
                      title={product.title}
                      taglist={product.tag}
                      img={`/my-game-mall/${product.image}`}
                      comment={product.comment}/>:''
                  ))
                }
                {/*   */}
                {(page*itemsPerPage - filteredProducts.length < 0)
                  ? <MoreButton onClick={onMore}></MoreButton>
                  : ''
                }
              </CardList>
            </Bottom>
          </GameListContainer>
          {/*    - chart.js? */}
          <GameChartContainer>
            <div>
              <MenuText>SUMMARY</MenuText>
              <Line/>
              <ChartContainer>
                <ChartDiv><MyChart data={about_platform} title=' ' type='bar'/></ChartDiv>
                <ChartDiv><MyChart data={about_genre} title=' ' type='doughnut'/></ChartDiv>
              </ChartContainer>
            </div>
          </GameChartContainer>
      </>
    )
  } 
}

export default MainPage